.descriptionBlock {
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  overflow-x: hidden;
  margin: 0 auto;
}

.header {
  color: #1cd119;
  font-size: 20px;
  letter-spacing: 3px;
  display: flex;
  flex-direction: row;
  align-items: center;

  @media (min-width: 650px) {
    color: #1cd119;
    font-size: 30px;
  }
}

.headerText {
  @media (min-width: 1024px) {
    font-size: 50px;
  }
}

.logo {
  width: 35px;
  margin-left: 10px;
  display: flex;
  align-items: center;

  @media (min-width: 650px) {
    width: 50px;
    height: 50px;
  }
}

.wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 40px;

  @media (min-width: 1024px) {
    flex-direction: row;
    align-items: flex-start;
    justify-content: space-between;
    gap: 40px;
    margin-top: 60px;
  }
}

.photoBlock {
  order: 2;
  margin: 40px 0 0;
  position: relative;
  border-radius: 15px;
  transition: 0.5s;
  width: 60%;
  max-width: 300px;
  max-height: 550px;
  overflow: hidden;

  @media (min-width: 650px) {
    margin: 40px auto 0;
    max-width: 400px;
  }

  @media (min-width: 1024px) {
    margin: 0;
    max-width: 400px;
    width: 40%;
  }

  @media (min-width: 1280px) {
    max-width: 450px;
  }
}

@media (min-width: 1024px) {
  .photoBlock:hover .myPhoto {
    transform: scale(1.15);
  }
}

.myPhoto {
  position: relative;
  top: 2px;

  width: 100%;
  border-radius: 15px;
  transition: 0.5s;
  z-index: 2;
}

.neo {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 1;
}

.photoHover {
  background: #1cd119;
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  z-index: 3;
  opacity: 0.45;
  border-radius: 15px;
  transition: 0.5s;
}

.photoHover:hover {
  opacity: 0;
}

.content {
  order: 1;

  @media (min-width: 1024px) {
    width: 58%;
    margin-top: 0px;
  }
}

.text {
  width: 80%;
  max-width: 1000px;
  margin: 0 auto 20px;

  font-style: normal;
  font-weight: 400;
  font-size: 20px;
  line-height: 130%;

  color: var(--white);

  @media (min-width: 650px) {
    font-size: 22px;
  }

  @media (min-width: 1024px) {
    text-align: start;
    width: 100%;
    max-width: 750px;
    font-size: 20px;
    line-height: 150%;
  }

  @media (min-width: 1280px) {
    font-size: 22px;
    line-height: 157%;
  }
}

.link {
  color: #1cd119;
  text-decoration: none;
  display: inline-block;
  cursor: pointer;
  transition: 0.2s;
}

.link:hover {
  text-shadow: 0 0 3px var(--matrixGreen);
}

.hello {
  display: block;
  width: 140px;
  height: 55px;
  margin: 40px auto 0;

  background: var(--darkGray);
  color: var(--matrixGreen);
  text-shadow: 0 0 5px var(--matrixGreen);
  box-shadow: 0 0 5px 1px var(--matrixGreen);
  border: 2px solid var(--matrixGreen);
  border-radius: 5px;
  cursor: pointer;

  font-weight: 600;
  font-size: 18px;
  transition: 0.5s;

  @media (min-width: 650px) {
    font-size: 25px;
    line-height: 51px;

    width: 180px;
    height: 70px;
  }

  @media (min-width: 1024px) {
    margin: 50px auto 0;
    box-shadow: 0 0 10px 2px var(--matrixGreen);
    padding: 0 0 3px 0;

    font-size: 30px;

    width: 200px;
    height: 80px;
  }

  @media (min-width: 1280px) {
    font-size: 35px;
    width: 230px;
    height: 85px;
  }
}

.hello:hover {
  color: #0a0a0a;
  background: #1cd119;
  border: 2px solid #1cd119;
  box-shadow: none;
  text-shadow: 0 0 5px var(--matrixGreen);
}

.hello:active {
  color: #1cd119;
  background: #0a0a0a;
  transition: 0.1s;
}
